






import {Component, Prop, Vue} from "vue-property-decorator"
import PageBackground from "@/components/PageBackground.vue"

@Component({ components: { PageBackground } })
export default class PageBgRotator extends Vue {
	@Prop()
	private blur!: boolean

	private interval: number = 12000
	private animateDuration: number = 4000
	private listenRotate: number | null = null
	private listenAnimate: number | null = null
	private animate: boolean | null = null
	private currIdx: number = 0
	private nextIdx: number = 0

	private images: string[] = ["bg1.jpg", "bg2.jpg", "bg3.jpg", "bg4.jpg", "bg5.jpg", "bg6.jpg"]

	private destroyed(): void {
		if (this.listenRotate !== null) clearTimeout(this.listenRotate)
		if (this.listenAnimate !== null) clearTimeout(this.listenAnimate)
	}

	private mounted(): void {
		setTimeout(() => this.flipImages(), this.interval + this.animateDuration)
		setTimeout(() => this.bgRotate(), this.interval)
	}

	private bgRotate(): void {
		this.nextIdx = this.calcIdx(this.nextIdx)
		this.animate = true
		setTimeout(() => this.flipImages(), this.interval + this.animateDuration)
		setTimeout(() => this.bgRotate(), this.interval)
	}

	private flipImages(): void {
		this.animate = false
		this.currIdx = this.calcIdx(this.currIdx)
	}

	private calcIdx(idx: number): number {
		return idx < this.images.length - 1 ? idx + 1 : 0
	}
}
